import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Fab } from '@material-ui/core';
import Icon from '@mdi/react'
import { mdiTeach } from '@mdi/js';
import { SpeedDial, SpeedDialIcon, SpeedDialAction } from '@material-ui/lab';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { useStore } from '../../hooks'

const useStyles = makeStyles(theme => ({    
    fab: {
      margin: theme.spacing(1),
    },
    speedDial: {
      position: 'absolute',
      bottom: theme.spacing(2),
      right: theme.spacing(2),
    },
}));

export function Teacher (props) {
    const classes = useStyles();   
    const [subject, setSubject] = useState('') 
    const [values, setValues] = useState({
        ass: [],
        tits: [],
    });
    const [anchorEl, setAnchorEl] = useState(null);
    const store = useStore(props)

    const handleClick = event => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = (e) => {
        setSubject(e)        
        setAnchorEl(null);
        store.teachher = e
    };
    return (        
        <div>
            <Fab color="primary" aria-label="teach her" className={classes.fab} onClick={handleClick}>
                <Icon path={mdiTeach }
                    title="teach her"
                    size={1}
                    color="red"
                />
            </Fab>
            <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={() => handleClose('Ass')}>Ass</MenuItem>
                <MenuItem onClick={() => handleClose('Asshole')}>Asshole</MenuItem> 
                <MenuItem onClick={() => handleClose('cock')}>cock</MenuItem>
                <MenuItem onClick={() => handleClose('Creampie')}>Creampie</MenuItem>
                <MenuItem onClick={() => handleClose('Cum')}>Cum</MenuItem>
                <MenuItem onClick={() => handleClose('Cunt')}>Cunt</MenuItem>
                <MenuItem onClick={() => handleClose('Face')}>Face</MenuItem> 
                <MenuItem onClick={() => handleClose('Gape')}>Gape</MenuItem>                
                <MenuItem onClick={() => handleClose('Hair')}>Hair</MenuItem>   
                <MenuItem onClick={() => handleClose('Hand')}>Hand</MenuItem>                                             
                <MenuItem onClick={() => handleClose('Heels')}>Heels</MenuItem>              
                <MenuItem onClick={() => handleClose('Lingerie')}>Lingerie</MenuItem>
                <MenuItem onClick={() => handleClose('Mouth')}>Mouth</MenuItem>
                <MenuItem onClick={() => handleClose('Pubes')}>Pubes</MenuItem>                                           
                <MenuItem onClick={() => handleClose('Stockings')}>Stockings</MenuItem>
                <MenuItem onClick={() => handleClose('Tits')}>Tits</MenuItem>
                <MenuItem onClick={() => handleClose('Toilet')}>Toilet</MenuItem>
            </Menu>
            {subject}
        </div> 
    );
}